const bufferAndStartLoop = (task: Function): Promise<Response> => {
  BufferManager.add(task);

  RequestLoop.start();

  return Promise.resolve(new Response());
};

class BufferedRequest {
  static async submit(task: Function): Promise<Response> {
    try {
      if (await Server.isSleeping()) {
        return bufferAndStartLoop(task);
      }

      return task();
      // return CashflowAPIService.postEntry(args);
    } catch (err) {
      return bufferAndStartLoop(task);
    }
  }
  // static async start(newEntry: NewEntryState): Promise<Response> {
  //   try {
  //     if (await Server.isSleeping()) {
  //       return bufferAndStartLoop(newEntry);
  //     }

  //     return CashflowAPIService.postEntry(newEntry);
  //   } catch (err) {
  //     return bufferAndStartLoop(newEntry);
  //   }
  // }
}

export default BufferedRequest;
